h1{
    margin: 0;
    padding: 0;
}
.site-header{
    background: darkslateblue;
    color: white;
    padding: 1rem;
    text-align: center;
}
.unit-converter{
    display: flex;
    min-width: 1190px;
    justify-content: center;
    margin: 2rem;
}
.ad{
    background: #eee;
    width: 400px;
    height: 300px;
}
.converter{
    padding: 1rem;
}
.converter ul{
    list-style: none;
}
.converter ul li{
    margin: 0 0 1rem 0;
}
.converter input{
    padding: .5rem 1rem;
    width: 80%;
}